import {Component, OnInit} from '@angular/core';
import {Apollo, gql} from 'apollo-angular';
import {navbarQuery} from '../../home/home/homequeries';
import {AboutQuery} from './aboutQuery';
import {DomSanitizer, SafeResourceUrl} from '@angular/platform-browser';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {
  loading = true;
  about: any;
  vision: any;
  mission: any;
  name = 'Set iframe source';
  urlSafe: SafeResourceUrl;
  urlSafe2: SafeResourceUrl;
  // url: string = 'https://my.matterport.com/show/?m=Kuem2vxoJmk&mls=1frameborder=0allowfullscreen allow=xr-spatial-tracking';
  url = 'https://mpembed.com/show/?m=Kuem2vxoJmk&minimap=1&mdir=1&lang=en&bgmusic=https://www.bensound.com/bensound-music/bensound-summer.mp3&play=1';
  // url2 = 'https://my.matterport.com/show/?m=cuzx3MPJykm';
  url2 = 'https://mpembed.com/show/?m=cuzx3MPJykm&minimap=1&mdir=1&lang=en&bgmusic=https://www.bensound.com/bensound-music/bensound-summer.mp3&play=1';
  // https://my.matterport.com/show/?m=cuzx3MPJykm

  constructor(private apollo: Apollo, public sanitizer: DomSanitizer) {
    this.getAbout();
  }

  ngOnInit() {
    this.urlSafe = this.sanitizer.bypassSecurityTrustResourceUrl(this.url);
    this.urlSafe2 = this.sanitizer.bypassSecurityTrustResourceUrl(this.url2);
  }

  getAbout() {
    this.apollo
      .watchQuery({
        query: AboutQuery
      })
      .valueChanges.subscribe((result: any) => {
      this.loading = result.loading;
      this.about = result.data?.getAboutKey;
      this.vision = result?.data?.getVisionKey;
      this.mission = result?.data?.getMissionKey;
    });
  }
}
